
body{
  padding: 0;
  margin: 0;
  color: #2c3e50;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #F6F6F6;
  font-size: 14px;
}
header {
  font-family: 'Cabin Sketch', 'Avenir', Helvetica, Arial, sans-serif;
  text-align: center;
  padding-top: 50px;
  padding-bottom: 15px;
  background: #f3f3f3;
}
main {
  margin: 0 auto;
  padding: 15px;
  max-width: 800px;
  margin-bottom: 100px;
  background: #fff;
}
blockquote {
  margin: 0;
  padding: 0.05em 1em;
  color: #6a737d;
  border-left: 0.25em solid #dfe2e5;
  background: #f3f3f3;
}
a{
  color: #0366D6;
  text-decoration: none;
}
.hljs{
  background: #FAFAFA;
}
code {
  color: #ED4C6B;
  background-color: #F6F8FA;
  margin: 0 4px;
  display: inline-block;
  padding: 1px 5px;
  font-size: 12px;
  border-radius: 3px;
  line-height: 18px;
  font-family: "SFMono-Regular",Consolas,"Liberation Mono",Menlo,Courier,monospace;
}
.doc-block table {
  border-collapse: collapse;
  width: 100%;
  background-color: #fff;
  font-size: 14px;
  margin-bottom: 45px;
  line-height: 1.5em;
  text-align: left;
}
.doc-block table td,
.doc-block table th
{
  border-bottom: 1px solid #d8d8d8;
  padding: 15px;
  max-width: 250px;
}

.layout-footer{
  height: 100px;
}
.copyright{
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style: none;
  color: #586069;
  font-weight: 100;
}
.copyright li {
  display: inline-block;
  margin-left: 10px;
}

.fork-img{
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
}

.demo-block{
  border: 1px solid #ebebeb;
  border-radius: 3px;
  -webkit-transition: .2s;
  transition: .2s;
  padding: 0px;
  margin: 10px 0;
}
.demo-block .source{
  padding: 24px;
}
.demo-block .description {
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #ebebeb;
  border-radius: 3px;
  font-size: 14px;
  line-height: 22px;
  color: #666;
  word-break: break-word;
  margin: 10px;
  background-color: #fff;
}
.demo-block .description p{
  padding: 0;
  margin: 0;
}
.demo-block .meta {
  background-color: #fafafa;
  border-top: 1px solid #eaeefb;
  overflow: hidden;
}
.demo-block body,
.demo-block .meta .hljs{
  background: transparent;
}
.m-dialog {
  width: 30%;
  margin: 0px auto 50px;
  margin-top: 15vh;
  position: relative;
  -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 3px;
          box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 3px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 2px;
  background: #fff;
}
.m-dialog__wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  margin: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 10000;
}
.m-dialog .m-dialog__header,
.m-dialog .m-dialog__body,
.m-dialog .m-dialog__footer {
  padding: 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.m-dialog .m-dialog__header {
  padding-bottom: 10px;
}
.m-dialog .m-dialog__title {
  font-size: 18px;
  color: #2c3e50;
}
.m-dialog .m-dialog__close {
  font-weight: 100;
  font-size: 20px;
  color: #909399;
  position: absolute;
  top: 11px;
  right: 15px;
  padding: 0;
  background: 0 0;
  border: none;
  outline: 0;
  cursor: pointer;
}
.m-dialog .m-dialog__close:hover {
  color: #ef4848;
}
.m-dialog .m-dialog__footer:before,
.m-dialog .m-dialog__footer:after {
  content: "";
  display: table;
  clear: both;
}
.dialog-fade-enter-active {
  -webkit-animation: dialog-fade-in 0.3s;
          animation: dialog-fade-in 0.3s;
}
.dialog-fade-leave-active {
  -webkit-animation: dialog-fade-out 0.3s;
          animation: dialog-fade-out 0.3s;
}
@-webkit-keyframes dialog-fade-in {
  0% {
    -webkit-transform: translate3d(0, -20px, 0);
            transform: translate3d(0, -20px, 0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
@keyframes dialog-fade-in {
  0% {
    -webkit-transform: translate3d(0, -20px, 0);
            transform: translate3d(0, -20px, 0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
@-webkit-keyframes dialog-fade-out {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate3d(0, -20px, 0);
            transform: translate3d(0, -20px, 0);
    opacity: 0;
  }
}
@keyframes dialog-fade-out {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate3d(0, -20px, 0);
            transform: translate3d(0, -20px, 0);
    opacity: 0;
  }
}
